.Navbar {
  position: fixed;
  height: 8vh;
  width: 75vw;
  left: 25%;
  background-color: rgb(31, 29, 29);
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  align-items: center;
  flex-direction: row;
  list-style: none;
  text-decoration: none;
}

.Navbar ul {
  display: flex;
  justify-content: space-around;
  width: 60vw;
  list-style: none;
  color: #ffb600;
}

.Navbar ul li {
  cursor: pointer;
  list-style: none;
}
.Navbar ul li a {
  text-decoration: none;
  color: white;
}
.Navbar ul li a:active {
  color: #ffb600;
}

.mode {
  height: 4vh;
  width: 2vw;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: rgb(120, 120, 120);
  box-shadow: 3px 4px 15px 1px #676262;
  border: none;
  cursor: pointer;
  transition: ease 2s;
}

.mode:hover {
  box-shadow: 3px 4px 15px 1px #817474;
}

.main {
